body{
  background-color: black;
}

.paper{
  width: 300px;
  height: 327px;
  margin: auto;
  background-image: linear-gradient(steelblue, skyblue);
  margin-top: 100px;
  padding-top: 30px;
  text-align: center;
  color: white !important;
  text-shadow: 2px 2px 8px black;
  box-shadow: 2px 2px 12px 2px green !important;
}

.fontcss{
  font-family: sans-serif;
  font-size: 15px;
}

.elementcenter{
  display: flex;
  justify-content: space-around;
}

.imgcss{
  background-color:lightblue;
  border-radius: 50%;
  box-shadow: 2px 2px 13px 2px white;
}
